.pagination-ls{--pagination-height: 40px;height:var(--pagination-height);display:inline-flex;align-items:center;--button-color: var(--color-primary-content)}.pagination-ls_disabled{--button-color: var(--color-neutral-content-subtlest);background-color:var(--color-neutral-surface);pointer-events:none}.pagination-ls__label{padding-right:16px;font-size:16px;line-height:19px;color:var(--color-neutral-content-subtler)}.pagination-ls__navigation{display:flex;height:100%;align-items:center;border-radius:5px;background:var(--color-neutral-background);border:1px solid var(--color-neutral-border)}.pagination-ls__navigation_waiting .pagination-ls__navigation{pointer-events:none;animation:button-waiting 1s linear infinite;--button-background-image: var(--button-waiting-animation-bg)}.pagination-ls__divider{width:1px;height:30px;background-color:var(--color-neutral-border)}.pagination-ls__btn{width:37px;height:100%;display:flex;align-items:center;justify-content:center;user-select:none;cursor:pointer}.pagination-ls__btn_waiting .pagination-ls__btn{opacity:.5}.pagination-ls__btn_disabled{pointer-events:none;--button-color: var(--color-neutral-content-subtlest)}.pagination-ls__btn_arrow-left,.pagination-ls__btn_arrow-right{position:relative}.pagination-ls__btn_arrow-left::before,.pagination-ls__btn_arrow-left::after,.pagination-ls__btn_arrow-right::before,.pagination-ls__btn_arrow-right::after{content:"";display:block;position:absolute;left:50%;top:50%;width:9px;height:9px;border:2px solid var(--color-primary-icon);border-bottom:none;border-right:none}.pagination-ls__btn_arrow-left::after,.pagination-ls__btn_arrow-right::after{display:none}.pagination-ls__btn_arrow-left::before,.pagination-ls__btn_arrow-left::after{transform:translate3d(-50%, -50%, 0) rotate(-45deg)}.pagination-ls__btn_arrow-right::before,.pagination-ls__btn_arrow-right::after{transform:translate3d(-50%, -50%, 0) rotate(135deg)}.pagination-ls__btn_arrow-left-double::after{display:block;margin-left:7px}.pagination-