.form{width:100%;display:block}.form__row{display:grid;justify-items:stretch;justify-content:space-between;grid-template-columns:repeat(var(--column-count, 5), 1fr);grid-gap:var(--row-gap, 16px) 12px}.form__row:not(:first-child){margin-top:20px}.form__row__description{font-size:.875rem}.form__submit{display:flex;margin-top:32px;align-items:center;justify-content:space-between}.form__submit+.inline-error{margin-top:32px}.form__submit_size_small{margin-top:16px}.form__info{display:flex;align-items:center;color:var(--color-negative-content);font-size:14px;line-height:22px}.form__info_valid{color:var(--color-neutral-content-subtler)}.form__column{display:grid;grid-auto-flow:column;align-items:flex-start}.input-ls,.textarea-ls,.counter,.select-ls__list{transition:all 100ms ease-out;outline:0;color:var(--color-neutral-content)}.input-ls:not(.input-ls_ghost,.textarea-ls_ghost,.counter_ghost,.select-ls__list_ghost):focus,.input-ls:not(:read-only):focus,.input-ls_focused,.textarea-ls:not(.input-ls_ghost,.textarea-ls_ghost,.counter_ghost,.select-ls__list_ghost):focus,.textarea-ls:not(:read-only):focus,.textarea-ls_focused,.counter:not(.input-ls_ghost,.textarea-ls_ghost,.counter_ghost,.select-ls__list_ghost):focus,.counter:not(:read-only):focus,.counter_focused,.select-ls__list:not(.input-ls_ghost,.textarea-ls_ghost,.counter_ghost,.select-ls__list_ghost):focus,.select-ls__list:not(:read-only):focus,.select-ls__list_focused{outline:4px solid var(--color-primary-focus-outline);border-color:var(--color-neutral-border-bolder)}.input-ls:focus-visible,.textarea-ls:focus-visible,.counter:focus-visible,.select-ls__list:focus-visible{outline:none}.input-ls:read-only:focus,.textarea-ls:read-only:focus,.counter:read-only:focus,.select-ls__list:read-only:focus{box-shadow:none;border-color:var(--border-color)}.form-indicator{font-weight:500}.form-indicator__item_type_success{color:var(--color-positive-content)}.form-indicator__item_type_fail{color:var(--color-negative-content)}