.toggle-items{--button-checked-shadow: 0 1px 0 rgba(var(--color-neutral-shadow-raw) / 10%), 0 5px 10px rgba(var(--color-neutral-shadow-raw) / 10%);display:flex;justify-content:stretch;list-style:none;padding:4px;margin:0;background:var(--color-neutral-surface);border:1px solid var(--color-neutral-border);border-radius:var(--corner-radius-small);font-weight:500}.toggle-items_big{font-size:16px}.toggle-items_big .toggle-items__item{padding:4px 20px}.toggle-items__item{cursor:pointer;color:var(--color-neutral-content-subtler);border-radius:4px;padding:2px 16px;flex-grow:1;text-align:center}.toggle-items__item_active{color:var(--color-neutral-content);background:var(--color-neutral-surface-hover);box-shadow:var(--button-checked-shadow)}