.sidebar{left:0;overflow:auto;position:fixed;z-index:100 !important;top:var(--header-height);height:calc(100vh - var(--header-height));width:var(--menu-sidebar-width);background:none;transition:all 150ms ease-out;border-radius:0}.sidebar .icon{transition:transform .1s ease;transform:rotate(0deg)}.sidebar_floating{box-shadow:0 0 0 9999px rgba(0,0,0,.2)}.sidebar_floating .unpin-menu{display:none}.sidebar:not(.sidebar_floating){box-shadow:0 1px 12px rgba(var(--color-neutral-shadow-raw)/8%),0 1px 4px 1px rgb(var(--color-neutral-shadow-raw)/12%)}.sidebar:not(.sidebar_floating) .pin-menu{display:none}.sidebar:not(.sidebar_floating).before-appear{opacity:1;transform:translate3d(-100%, 0, 0)}.sidebar:not(.sidebar_floating).appear{opacity:1;transform:translate3d(0, 0, 0)}.sidebar:not(.sidebar_floating).before-disappear{opacity:1;transform:translate3d(0, 0, 0)}.sidebar:not(.sidebar_floating).disappear{opacity:1;transform:translate3d(-100%, 0, 0)}.sidebar:not(.sidebar_floating).appear~.content-wrapper__content,.sidebar:not(.sidebar_floating).visible~.content-wrapper__content{margin-left:240px}.sidebar:not(.sidebar_floating).disappear~.content-wrapper__content{margin-left:0}.sidebar:not(.sidebar_floating) .sidebar__pin .icon{opacity:1;transform:rotate(-45deg)}