.breadcrumbs{height:100%;display:flex;align-items:center;margin-right:20px}.breadcrumbs__label{display:flex;gap:4px;align-items:center}.breadcrumbs__beta{font-size:12px;font-style:normal;font-weight:500;line-height:16px;color:var(--plum_0);padding:2px 8px;background-color:var(--plum_500);border-radius:12px}.breadcrumbs__list{height:100%;display:flex;align-items:center;list-style-type:none;margin:0;padding:0}.breadcrumbs__item{font-size:16px;line-height:22px;position:relative;margin:0;padding:0;height:100%;display:flex;align-items:center;cursor:default}.breadcrumbs__item:not(.breadcrumbs__item_last){cursor:pointer}.breadcrumbs__item:not(.breadcrumbs__item_last)>span,.breadcrumbs__item:not(.breadcrumbs__item_last)>a{color:var(--color-neutral-content-subtler)}.breadcrumbs__item:not(.breadcrumbs__item_last)>span:hover,.breadcrumbs__item:not(.breadcrumbs__item_last)>a:hover{color:var(--color-primary-content-hover)}.breadcrumbs__item:not(:nth-child(2)){flex-shrink:0}.breadcrumbs__item:nth-child(2) span{overflow:hidden;text-overflow:ellipsis;white-space:nowrap}.breadcrumbs__item:only-child{pointer-events:none}.breadcrumbs__item>span,.breadcrumbs__item>a{text-decoration:none;color:var(--color-neutral-content)}.breadcrumbs__item>span a,.breadcrumbs__item>a a{color:var(--color-primary-content)}.breadcrumbs__item+.breadcrumbs__item{margin-left:30px}.breadcrumbs__item+.breadcrumbs__item::before{top:50%;right:100%;width:30px;content:"/";height:16px;display:block;color:var(--color-neutral-content-subtlest);position:absolute;transform:translate3d(0, -50%, 0);text-align:center;line-height:16px;font-size:18px}.breadcrumbs__settings{width:20px;height:20px;display:block;margin-left:10px}.breadcrumbs__settings img{display:block;opacity:.23}