.sidebar-menu{flex:1;display:flex;max-height:calc(100vh - var(--header-height))}.sidebar-menu__navigation{width:calc(var(--menu-sidebar-width));display:flex;transition:border 400ms ease-out}.sidebar-menu__navigation .main-menu{background:var(--color-neutral-background)}.sidebar-menu__navigation .main-menu__item{background:var(--color-neutral-background)}.sidebar-menu__navigation .main-menu__item_active{background:var(--color-primary-emphasis-subtle);pointer-events:all}.sidebar-menu__content{flex:1;overflow:auto;padding:2rem}