.content-wrapper{display:flex;min-height:100vh;flex-direction:column;background-color:var(--color-neutral-background)}.content-wrapper__body{flex:1;display:flex;min-width:100%;min-height:100%;flex-direction:column;max-height:calc(100vh - var(--header-height))}.content-wrapper__content{flex:1;display:flex;flex-direction:column;margin-left:0;height:100%;will-change:margin-left;transition:background-color 400ms ease-out}.content-wrapper__content_withSidebar{margin-left:var(--menu-sidebar-width)}