.counter{width:114px;height:40px;display:flex;min-width:114px;border-radius:8px;background:var(--color-neutral-surface);box-sizing:border-box;border:1px solid var(--color-neutral-border);transition:all 150ms ease;align-items:center}.counter:active{border:1px solid var(--color-neutral-border-bold)}.counter_disabled{opacity:.6;background-color:var(--color-neutral-background)}.counter__btn{min-width:32px;min-height:32px;margin:4px;border-radius:4px;background:var(--color-neutral-background);display:flex;color:var(--color-primary-icon);border:none;outline:none;align-items:center;justify-content:center;transition:all 150ms ease;box-shadow:0 4px 8px rgba(var(--color-neutral-shadow-raw)/16%),0 1px 2px rgba(var(--color-neutral-shadow-raw)/30%)}.counter__btn_disabled{box-shadow:none;pointer-events:none;color:var(--color-neutral-content-subtlest);background:var(--color-neutral-surface)}.counter__btn:active,.counter__btn:hover{background:var(--color-neutral-surface-hover);color:var(--color-primary-content);box-shadow:0 6px 12px 0 rgba(0,0,0,.15),0 2px 4px 0 rgba(38,38,38,.3);box-shadow:0 6px 12px rgba(var(--color-neutral-shadow-raw)/16%),0 2px 4px rgba(var(--color-neutral-shadow-raw)/30%)}.counter__input{flex:1;width:100%;border:none;padding:0;background:none;text-align:center;outline:none;font-size:16px;line-height:22px;color:var(--color-neutral-content)}