.hamburger{width:18px;height:14px;cursor:pointer;position:relative;display:inline-block}.hamburger span{height:2px;width:100%;display:block;position:absolute;background-color:var(--color-neutral-content)}.hamburger span:nth-child(1){top:0}.hamburger span:nth-child(2){top:50%;transform:translate3d(0, -50%, 0)}.hamburger span:nth-child(3){bottom:0}.hamburger_animated span{transition:all var(--menu-animation-duration) ease}.hamburger:hover span,.hamburger_opened span{background-color:var(--color-neutral-content)}.hamburger_opened span:nth-child(1){top:6px;transform:rotate(135deg)}.hamburger_opened span:nth-child(2){opacity:0;transform:translate3d(-100%, -50%, 0)}.hamburger_opened span:nth-child(3){bottom:6px;transform:rotate(-135deg)}