.create-project{flex:1;min-height:0;display:flex;flex-direction:column;overflow-y:auto}.create-project .modal-ls__header{border-bottom:1px solid var(--color-neutral-border)}.create-project .modal-ls__header h1{width:224px;margin-right:auto;margin-bottom:0;margin-top:0;font-size:24px;color:var(--color-neutral-content)}.create-project .modal-ls__header button{line-height:1em;min-width:100px}.create-project .modal-ls__header .toggle-items{width:500px;margin-right:auto;box-sizing:border-box}.create-project .modal-ls__header .space-ls_direction_horizontal{min-width:224px}.create-project__tab_disabled{display:flex;align-items:center;justify-content:center}.create-project__tab_disabled::before{content:"";display:block;height:8px;width:8px;border-radius:4px;margin-right:6px;background:var(--color-negative-content)}.create-project form.project-name{width:500px;margin:32px auto}.create-project form.project-name>*:not(:first-child){margin-top:32px}.create-project form.project-name label{display:inline-flex;align-items:center}.create-project form.project-name input,.create-project form.project-name textarea{background:var(--color-neutral-background);border:1px solid var(--color-neutral-border);color:var(--color-neutral-content);border-radius:var(--corner-radius-smaller);box-shadow:inset 0 1px 2px rgba(var(--color-neutral-shadow-raw)/12%);transition:all 150ms ease-out}.create-project form.project-name input:hover,.create-project form.project-name textarea:hover{border-color:var(--color-neutral-border-bold)}.create-project form.project-name input:focus,.create-project form.project-name input:focus-visible,.create-project form.project-name textarea:focus,.create-project form.project-name textarea:focus-visible{border-color:var(--color-neutral-border-bolder);outline:none;box-shadow:0 0 0 4px var(--color-primary-focus-outline)}