.people-list{width:100%;min-width:650px;font-size:16px}.people-list__wrapper{border-radius:4px;border:1px solid var(--color-neutral-border);background:var(--color-neutral-background)}.people-list__loading{width:100%;display:flex;min-height:250px;justify-content:center;align-items:center}.people-list__users{width:100%;text-align:left;border-collapse:collapse}.people-list__header{font-weight:500;display:flex}.people-list__column,.people-list__field{z-index:2;height:48px;padding:0 10px;border:none;color:var(--color-neutral-content);display:flex;position:relative;align-items:center}.people-list__column.avatar,.people-list__field.avatar{width:28px;box-sizing:content-box}.people-list__column.name,.people-list__field.name{flex:1}.people-list__column.email,.people-list__field.email{width:200px}.people-list__column.last-activity,.people-list__field.last-activity{width:160px}.people-list__field.last-activity{color:var(--color-neutral-content)}.people-list__column{font-weight:500;background:none}.people-list__user{cursor:pointer;display:flex}.people-list__user:nth-child(odd):not(.people-list__user_active){background-color:var(--color-neutral-emphasis-subtle)}.people-list__user_active{position:relative;background-color:var(--color-primary-surface)}.people-list__user_active::before,.people-list__user_active::after{top:0;bottom:0;left:0;content:"";display:block;position:absolute}.people-list__user_active::after{z-index:1;background-color:var(--color-neutral-surface);width:calc(100% + 22px);box-shadow:inset 4px 0 0 var(--grape_500),1px 0 0 1px var(--color-neutral-border) inset}.people-list__user_active::before{z-index:0;height:100%;width:calc(100% + 22px)}