.input-ls,.select-ls,.textarea-ls{--input-size: 40px;height:var(--input-size);min-height:var(--input-size);background:var(--color-neutral-background);font-size:16px;line-height:22px;border:1px solid var(--color-neutral-border);box-sizing:border-box;border-radius:5px;padding:0 16px;transition:all 150ms ease-out;font-weight:400;color:var(--color-neutral-content);box-shadow:inset 0 1px 2px rgba(var(--color-neutral-shadow-raw)/8%)}.input-ls::placeholder,.select-ls::placeholder,.textarea-ls::placeholder{color:var(--color-neutral-content-subtler)}.input-ls_ghost,.select-ls_ghost,.textarea-ls_ghost{border:none;padding:0;background-color:rgba(0,0,0,0);outline:none}.input-ls:read-only,.select-ls:read-only,.textarea-ls:read-only{background-color:var(--color-neutral-surface);color:var(--color-neutral-content-subtler)}.input-ls:not([disabled]):hover,.textarea-ls:not([disabled]):hover{border-color:var(--color-neutral-border-bold)}.input-ls:not([disabled]):active,.textarea-ls:not([disabled]):active{border-color:var(--color-neutral-border-bolder)}.select-ls:not(.disabled):hover{border-color:var(--color-neutral-border-bold)}.select-ls:not(.disabled):active{border-color:var(--color-neutral-border-bolder)}input.input-ls[type=radio]{width:16px;height:16px;min-height:0}.textarea-ls{padding:12px 16px;min-height:50px}