.storage-settings__controls{display:flex;flex-direction:column;gap:8px;margin-bottom:32px}.storage-settings__empty{height:100px;display:flex;font-size:16px;font-weight:500;border-radius:5px;align-items:center;justify-content:center;flex-direction:column;color:var(--color-neutral-content-subtler)}.storage-settings__submit{display:flex;margin-top:32px;align-items:center;justify-content:space-between}.storage-settings__submit+.inline-error{margin-top:32px}.storage-settings__info{display:flex;align-items:center;color:#d00;font-size:14px;line-height:22px}.storage-settings__info_valid{color:rgba(0,0,0,.4)}.storage-settings__sync{display:flex;margin-top:16px;justify-content:flex-start;align-items:center}.storage-settings__sync-count{margin-top:14px;line-height:18px;font-size:14px;opacity:.7}.storage-settings__summary .dl__dd{min-width:0;white-space:normal;overflow-wrap:anywhere;word-break:break-word}