.user-info{padding:20px 24px;border:1px solid var(--color-neutral-border);background:var(--color-neutral-surface);align-self:start;display:flex;flex-direction:column;position:relative;border-radius:4px;min-height:100%}.user-info__header{display:grid;grid-template:auto/64px auto;column-gap:16px;line-height:1.5;color:var(--color-neutral-content);margin-bottom:16px;align-items:center}.user-info__email{margin:0}.user-info__full-name{font-size:1.5em;margin:0;font-weight:500;line-height:1.2;color:var(--color-neutral-content)}.user-info__section+.user-info__section{margin-top:16px}.user-info__section-title{margin-top:1em;margin-bottom:8px;font-weight:500;color:var(--color-neutral-content)}.user-info__last-active{color:var(--color-neutral-content-subtler);margin-top:32px;margin-bottom:0}.user-info__links-list{display:flex;flex-direction:column}.user-info__project-link{height:36px;display:flex;padding:0 10px;font-size:16px;margin-left:-10px;align-items:center;border-radius:4px;color:var(--color-primary-content)}.user-info__project-link:hover{background-color:var(--color-primary-emphasis-subtle)}