.main-menu{flex:1;margin:0;padding:8px;display:flex;flex-direction:column;list-style-type:none;max-width:100%;box-sizing:border-box;background:var(--color-neutral-background);border-radius:0;transition:background-color 400ms ease-out;border-right:1px solid var(--color-neutral-border);gap:2px}.main-menu__item{height:40px;display:flex;padding:0 13px;border-radius:var(--corner-radius-smaller);align-items:center;box-sizing:border-box;color:var(--color-neutral-content-subtler);font-size:1rem;white-space:nowrap;text-decoration:none;cursor:pointer;transition:all 150ms ease-out}.main-menu__item-icon{margin-right:10px;object-fit:contain;opacity:.5;display:flex;align-items:center;justify-content:center;width:24px}.main-menu__item-beta{background-color:var(--color-accent-plum-base);color:var(--color-accent-plum-subtlest);font-size:12px;font-style:normal;font-weight:500;line-height:16px;margin-left:8px;padding:2px 8px;border-radius:12px}.main-menu__item_look_danger{color:var(--color-negative-content)}.main-menu__item:not(.main-menu__item_look_danger):hover{color:var(--color-neutral-content);background:var(--color-primary-emphasis-subtle)}.main-menu__item:not(.main-menu__item_active):hover{color:var(--color-neutral-content);background:var(--color-primary-emphasis-subtle)}.main-menu__item_active{color:var(--color-neutral-content);font-weight:500}.main-menu__item_active:not(.sidebar__pin){pointer-events:none;background:var(--color-neutral-emphasis)}.main-menu__item:hover .main-menu__item-icon,.main-menu__item_active .main-menu__item-icon{opacity:1}.main-menu__item_dangerous{color:var(--color-negative-content)}.main-menu__item_dangerous:hover{color:var(--color-neutral-content) !important;background-color:var(--color-negative-emphasis-subtle) !important}.main-menu__spacer{flex:1}.main-menu__divider{height:1px;margin:8px 0;background-color:var(--color-neutral-border);transition:background-color 150ms ease-out}.main-menu_size_compact{background:var(--color-neutral-background)}.main-menu_size_compact .main-menu__item,.main-menu_size_medium .main-menu__item{height:32px;font-size:16px}.main-menu_size_small .main-menu__item{height:24px;font-size:14px;padding:0 10px}.main-menu_collapsed{padding:.5rem}.main-menu_collapsed__item{border-radius:.25rem}.main-menu_contextual{box-shadow:0 1px 2px rgba(38,38,38,.3),0 1px 3px 1px rgba(38,38,38,.15);border-radius:4px}.main-menu_contextual .main-menu__item{height:32px;padding:16px 8px;display:flex;align-items:center;justify-content:space-between;border-radius:4px;cursor:pointer}.main-menu_contextual .main-menu__item:hover{background-color:var(--color-primary-emphasis-subtle)}.main-menu_contextual .main-menu__item_dangerous{color:var(--color-negative-content)}.main-menu_contextual .main-menu__item_dangerous:hover{color:var(--color-neutral-content);background-color:var(--color-negative-emphasis-subtle)}.main-menu:first-child{padding-top:8px}.main-menu:last-child{padding-bottom:8px}.menu-group__title{padding:4px 10px;font-size:14px;color:var(--color-neutral-content-subtler)}.menu-group__list{padding:0;margin-left:10px;list-style-type:none}