.version-notifier{display:flex;padding:10px 0;color:var(--primary_link);margin:8px 0;background:var(--color-primary-surface-content);border-radius:5px}.version-notifier a{display:flex}.version-notifier a:visited{color:var(--primary_link)}.version-notifier__icon{width:46px;padding-top:1px;color:var(--primary_link)}.version-notifier__icon img{width:20px;height:20px;margin:0 auto;display:block}.version-notifier__content{flex:1;padding-right:12px}.version-notifier__title{font-weight:500;line-height:22px;font-size:16px;display:flex;justify-content:space-between;align-items:flex-end}.version-notifier__title::after{opacity:.5;line-height:22px;font-size:14px;content:attr(data-date)}.version-notifier__description{padding-top:8px;opacity:.6;font-size:14px;line-height:18px}.current-version{display:flex;cursor:pointer;padding:0 13px;margin-top:5px;margin-left:5px;border-radius:3px;box-sizing:border-box;color:var(--color-neutral-content-subtler);font-size:12px;white-space:nowrap;text-decoration:none}.current-version:hover{color:var(--primary_link)}